$메인컬러 : #ff0000;

.red {
    color: $메인컬러;
}
.my-alert {
    background: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;

    p {
        margin-bottom: 0;
    }
}

.my-alert2 {
    @extend .my-alert;
    background-color: #efd591;
}

@mixin name() {
    background-color: #eee;
    padding: 5px;
    border-radius: 5px;
    max-width: 500px;
    margin: 0 auto;
}

.my-alert3 {
    @include name();
}

